.form-styles {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  width: 600px;
  height: 300px;
  border: 2px solid rgb(148, 99, 99);
  border-radius: 10px;
  box-shadow: 0 0 5px rgb(128, 110, 110);
}

.label-form {
  margin: 0;
  margin-top: 10px;
  padding: 0;
  font-size: 20px;
  font-style: italic;
  font-weight: 600;
  display: flex;
  flex-direction: column;
}

.input-form {
  margin-top: 5px;
  padding: 10px 0;
  padding-left: 10px;
  width: 97%;
  font-size: 14px;
  border: 2px solid rgb(158, 114, 114);
  border-radius: 5px;
}

.input-form:focus-visible {
  outline: none !important;
  border-color: #017c01;
}

.button-form {
  margin: 0 auto;
  margin-top: 40px;
  padding: 10px 0;
  width: 100%;
  color: #e4e1e1;
  background-color: #1d37aa;
  border: 1px solid rgb(204, 203, 203);
  border-radius: 5px;
  outline: none;
}

.button-form:hover {
  background-color: #3756e4;
}
